'use client';

import { useEffect, useState } from 'react';
import { useApi } from '../../src/hooks/useApi';
import { useAuth } from '@/app/hooks/';

export default function ContactsDashboard() {
  const { get, post } = useApi();
  const { role } = useAuth();
  const [contacts, setContacts] = useState([]);

  useEffect(() => {
    const fetchContacts = async () => {
      const { data, ok } = await get('http://localhost:3000/contacts');

      if (ok) {
        setContacts(data);
      } else {
        console.error('Failed to fetch contacts');
      }
    };

    fetchContacts();
  }, []);

  const handleBanContact = async (contactId) => {
    const { ok } = await post(`http://localhost:3000/contacts/${contactId}/ban`, {});

    if (ok) {
      alert('Contact banned');
      // Refetch contacts
      const { data } = await get('http://localhost:3000/contacts');
      setContacts(data);
    } else {
      alert('Failed to ban contact');
    }
  };

  return (
    <div>
      <h1>Contacts Dashboard</h1>

      {(role === 'MODERATOR' || role === 'ADMIN') && (
        <button onClick={() => alert('Add contact - TODO')}>Add Contact</button>
      )}

      <ul>
        {contacts.map((contact) => (
          <li key={contact.id}>
            {contact.name} - {contact.email} - Status: {contact.status}

            {(role === 'MODERATOR' || role === 'ADMIN') && (
              <>
                <button onClick={() => alert('Edit contact - TODO')}>Edit</button>
                <button onClick={() => alert('Delete contact - TODO')}>Delete</button>
                {contact.status !== 'BANNED' && (
                  <button onClick={() => handleBanContact(contact.id)}>Ban</button>
                )}
              </>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}